import React from "react";
import TrippyElement1 from "../components/three/TrippyElement1/TrippyElement1"
import TrippyElement2 from "../components/three/TrippyElement2/TrippyElement2"
import TrippyElement3 from "../components/three/TrippyElement3/TrippyElement3"
import SkullElement from "../components/three/SkullElement/SkullElement"
import TrippyElement4 from "../components/three/TrippyElement4/TrippyElement4"
import TrippyElement5 from "../components/three/TrippyElement5/TrippyElement5"
import TrippyElement6 from "../components/three/TrippyElement6/TrippyElement6"
import TrippyElement7 from "../components/three/TrippyElement7/TrippyElement7"

import WaterElement from "../components/three/WaterElement/WaterElement"
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';


import Header from "../components/Header"
import {getInstanceContract,getWeb3}  from "../Web3Util";

class NFTPage extends React.Component {
 
  constructor(props){
    super(props)
    this.state={
      owner:"",
      indexerBaby:false,
      indexerGold:false,
      typeIndexer:-1
    }
    this.loadData = this.loadData.bind(this)
  }

    componentDidMount = async () => {
      try{
        const web3 = await getWeb3();
        window.web3 = web3;
        const contractInstance = await getInstanceContract(window.web3);

        this.setState({ user: window.user });
        this.setState({ contractInstance});
        await this.loadData();
      } catch (e) {
        console.log('Error', e)
      }finally{
        this.setState({
          isLoadingHomepageData:false
        })
      }
    }

    async loadData() {
      try {
        let  ID  = window.location.href.split("token/")[1]

          const [owner,indexerGold,indexerBaby,typeIndexer] = await Promise.all([
          this.state.contractInstance.methods.ownerOf(ID).call(), 
          this.state.contractInstance.methods.typeIndexerGold(ID).call(), 
          this.state.contractInstance.methods.typeIndexerBaby(ID).call(), 
          this.state.contractInstance.methods.typeIndexer(ID).call(), 

        ])

        this.setState({
          owner,
          typeIndexer,
          indexerBaby,
          indexerGold
        })
     
        
        // this.setState({hasHomepageData:true,dataResult:result})
      } catch (e) {
        console.log('Error', e)
      }
    }

    render() {
      let element
      if(this.state.typeIndexer == 0){
        element = <TrippyElement1 key={this.state.typeIndexer} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isBaby={this.state.indexerBaby} isGold={this.state.indexerGold}/>
      } else if(this.state.typeIndexer == 1){
        element = <TrippyElement2 key={this.state.typeIndexer} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isBaby={this.state.indexerBaby} isGold={this.state.indexerGold}/>
      }else if(this.state.typeIndexer == 2){
        element = <WaterElement key={this.state.typeIndexer} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isBaby={this.state.indexerBaby} isGold={this.state.indexerGold}/>
      }else if(this.state.typeIndexer == 3){
        element = <TrippyElement3 key={this.state.typeIndexer} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isBaby={this.state.indexerBaby} isGold={this.state.indexerGold}/>
      }else if(this.state.typeIndexer == 4){
        element = <SkullElement key={this.state.typeIndexer}  shouldRotateAutomatically={true} hasRandomInitialPosition={true} isBaby={this.state.indexerBaby} isGold={this.state.indexerGold}/>
      }else if(this.state.typeIndexer == 5){
        element = <TrippyElement4 key={this.state.typeIndexer} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isBaby={this.state.indexerBaby} isGold={this.state.indexerGold}/>
      }else if(this.state.typeIndexer == 6){
        element = <TrippyElement5 key={this.state.typeIndexer} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isBaby={this.state.indexerBaby} isGold={this.state.indexerGold}/>
      }else if(this.state.typeIndexer == 7){
        element = <TrippyElement6 key={this.state.typeIndexer} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isBaby={this.state.indexerBaby} isGold={this.state.indexerGold}/>
      }else if(this.state.typeIndexer == 8){
        element = <TrippyElement7 key={this.state.typeIndexer} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isBaby={this.state.indexerBaby} isGold={this.state.indexerGold}/>
      }
      let  ID  = window.location.href.split("token/")[1]

      return (

        <div className="nft-page-container  " style={{minHeight:"140vh"}}>
        <Header />

         
          <Container  maxWidth="sm">
          <Grid container className="nft-page-container">
            <Grid item md={8} xs={12} style={{margin:"auto",overflow:"hidden"}} className="nft-page-container">
                {element}
            </Grid>
          </Grid>

            {this.state.owner != ""?
            <Grid container>
              <Grid className="crop-text" item md={12} xs={12} style={{margin:"auto",overflow:"hidden",textAlign:"center"}} >
                <h1 className="crop-text">Owner: {this.state.owner}</h1>
                <h2>NFT ID: {ID}</h2>
              </Grid>
            </Grid>
            :null}
          <br />
          <br />
          <br />
          <br />
          </Container>
          
        </div>
      );
    }
  }
export default NFTPage;