import React from "react";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';

import Header from "../components/Header"

import {getInstanceContract,getWeb3}  from "../Web3Util";

import {Link} from "react-router-dom"
import Web3 from "web3";
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';

class WalletPage extends React.Component {

    constructor(props){
      super(props)
      this.state={
        tokenIDs:[],
        upgradeResults:[]
      }
      this.loadData = this.loadData.bind(this)
      this.getWalletItems = this.getWalletItems.bind(this)
      this.getUpgradeInfoComponent = this.getUpgradeInfoComponent.bind(this)
      this.upgradeGold = this.upgradeGold.bind(this)
      this.upgradeBaby = this.upgradeBaby.bind(this)
    }
    componentDidMount = async () => {
      try{
        const web3 = await getWeb3();
        window.web3 = web3;
        const contractInstance = await getInstanceContract(window.web3);
  
        this.setState({ user: window.user });
        this.setState({ contractInstance});
        await this.loadData();
      } catch (e) {
        console.log('Error', e)
      }finally{
        this.setState({
          isLoadingHomepageData:false
        })
      }
    }

    async loadData() {
      try {

        const upgradeResults = await Promise.all([
          this.state.contractInstance.methods.pricesBaby(0).call(), 
          this.state.contractInstance.methods.pricesBaby(1).call(), 
          this.state.contractInstance.methods.pricesBaby(2).call(), 
          this.state.contractInstance.methods.pricesBaby(3).call(), 
          this.state.contractInstance.methods.pricesBaby(4).call(), 
          this.state.contractInstance.methods.pricesBaby(5).call(), 
          this.state.contractInstance.methods.pricesBaby(6).call(), 
          this.state.contractInstance.methods.pricesBaby(7).call(), 
          this.state.contractInstance.methods.pricesBaby(8).call(), 
          
          this.state.contractInstance.methods.maxBaby(0).call(), 
          this.state.contractInstance.methods.maxBaby(1).call(), 
          this.state.contractInstance.methods.maxBaby(2).call(), 
          this.state.contractInstance.methods.maxBaby(3).call(), 
          this.state.contractInstance.methods.maxBaby(4).call(), 
          this.state.contractInstance.methods.maxBaby(5).call(), 
          this.state.contractInstance.methods.maxBaby(6).call(), 
          this.state.contractInstance.methods.maxBaby(7).call(), 
          this.state.contractInstance.methods.maxBaby(8).call(), 

          this.state.contractInstance.methods.currentCounterBaby(0).call(), 
          this.state.contractInstance.methods.currentCounterBaby(1).call(), 
          this.state.contractInstance.methods.currentCounterBaby(2).call(), 
          this.state.contractInstance.methods.currentCounterBaby(3).call(), 
          this.state.contractInstance.methods.currentCounterBaby(4).call(), 
          this.state.contractInstance.methods.currentCounterBaby(5).call(), 
          this.state.contractInstance.methods.currentCounterBaby(6).call(), 
          this.state.contractInstance.methods.currentCounterBaby(7).call(), 
          this.state.contractInstance.methods.currentCounterBaby(8).call(), 

          this.state.contractInstance.methods.pricesGold(0).call(), 
          this.state.contractInstance.methods.pricesGold(1).call(), 
          this.state.contractInstance.methods.pricesGold(2).call(), 
          this.state.contractInstance.methods.pricesGold(3).call(), 
          this.state.contractInstance.methods.pricesGold(4).call(), 
          this.state.contractInstance.methods.pricesGold(5).call(), 
          this.state.contractInstance.methods.pricesGold(6).call(), 
          this.state.contractInstance.methods.pricesGold(7).call(), 
          this.state.contractInstance.methods.pricesGold(8).call(), 
          
          this.state.contractInstance.methods.maxGold(0).call(), 
          this.state.contractInstance.methods.maxGold(1).call(), 
          this.state.contractInstance.methods.maxGold(2).call(), 
          this.state.contractInstance.methods.maxGold(3).call(), 
          this.state.contractInstance.methods.maxGold(4).call(), 
          this.state.contractInstance.methods.maxGold(5).call(), 
          this.state.contractInstance.methods.maxGold(6).call(), 
          this.state.contractInstance.methods.maxGold(7).call(), 
          this.state.contractInstance.methods.maxGold(8).call(), 

          this.state.contractInstance.methods.currentCounterGold(0).call(), 
          this.state.contractInstance.methods.currentCounterGold(1).call(), 
          this.state.contractInstance.methods.currentCounterGold(2).call(), 
          this.state.contractInstance.methods.currentCounterGold(3).call(), 
          this.state.contractInstance.methods.currentCounterGold(4).call(), 
          this.state.contractInstance.methods.currentCounterGold(5).call(), 
          this.state.contractInstance.methods.currentCounterGold(6).call(), 
          this.state.contractInstance.methods.currentCounterGold(7).call(), 
          this.state.contractInstance.methods.currentCounterGold(8).call(), 
        ])

        // console.log("@@@ upgradeResults",upgradeResults)
        this.setState({upgradeResults})


        let balanceOf = await this.state.contractInstance.methods.balanceOf(window.user).call()
        let tokenIDs = []
        let types = []
        let isGolds = []
        let isBabys = []
        for(let counter=0;counter<balanceOf;++counter){
          let tokenID = await this.state.contractInstance.methods.tokenOfOwnerByIndex(window.user,counter).call()
          tokenIDs.push(tokenID)

          const result = await Promise.all([
            this.state.contractInstance.methods.typeIndexer(tokenID).call(),
            this.state.contractInstance.methods.typeIndexerGold(tokenID).call(),
            this.state.contractInstance.methods.typeIndexerBaby(tokenID).call(),
          ])
          types.push(result[0])
          isGolds.push(result[1])
          isBabys.push(result[2])
        }

        
        this.setState({
          hasHomepageData:true,
          tokenIDs,
          types,
          isGolds,
          isBabys
        })
       
      } catch (e) {
        console.log('Error', e)
      }
    }

    async upgradeGold(tokenID,type){
      try{
        await this.state.contractInstance.methods.upgradeGold(tokenID).send({
          value:this.state.upgradeResults[Number.parseInt(type)+27]
        })

      } catch (e) {
        console.log('Error', e)
      }finally{
        await this.loadData();
      }
    }


    async upgradeBaby(tokenID,type){
      try{
        await this.state.contractInstance.methods.upgradeBaby(tokenID).send({
          value:this.state.upgradeResults[Number.parseInt(type)]
        })

      } catch (e) {
        console.log('Error', e)
      }finally{
        await this.loadData();
      }
    }

    getWalletItems(){
      let components = []

      for(let counter=0;counter<this.state.types.length;++counter){
        components.push(
          <Grid item md={4} xs={6}>
            <Card style={{padding:"10px",paddingTop:"15px"}}>
            <div style={{textAlign:"center"}}>ID: {this.state.tokenIDs[counter]}, Type:  {Number.parseInt(this.state.types[counter])+1}</div>
            <br />
            <Button style={{width:"100%"}}><Link to={"/token/"+this.state.tokenIDs[counter]} style={{color:"black",fontWeight:"bold",textDecoration:"none"}}>View NFT  </Link></Button>
            <br />
            <br />
            {this.state.isGolds[counter] == false?
              <>
                <Button style={{width:"100%"}} onClick={()=>this.upgradeGold(this.state.tokenIDs[counter],this.state.types[counter])}>Upgrade to gold</Button>
              </>
            :
              <>
                <Button style={{width:"100%"}} disabled>NFT has Gold upgrade</Button>
              </>
              }
            <br />
            <br />
            {this.state.isBabys[counter] == false?
              <>
                <Button style={{width:"100%"}} onClick={()=>this.upgradeBaby(this.state.tokenIDs[counter],this.state.types[counter])}>Upgrade to baby</Button>
              </>
            :
            
            <>
              <Button style={{width:"100%"}} disabled>NFT has Baby upgrade</Button>
            </>
            }
            <br />
            <br />
            
            </Card>
          </Grid>
        )
      }

      return components
    }

    getUpgradeInfoComponent(){
      let components = []

      for(let counter=0;counter<9;++counter){
        components.push(
          <Grid item md={4} xs={6}>
            <Card style={{padding:"10px",paddingTop:"15px"}}>

            <b>Type: {counter+1}</b>
            <br />
            <br />
            Baby upgrade price: <br /> {Web3.utils.fromWei(this.state.upgradeResults[counter]+"", 'ether')} {process.env.REACT_APP_BLOCKCHAIN} 
            <br /> <br />
            Baby upgrades left:  <br />{this.state.upgradeResults[counter+9]-this.state.upgradeResults[counter+18]} / {this.state.upgradeResults[counter+9]} 
            <br />
            <br />
            Gold upgrade price:  <br />{Web3.utils.fromWei(this.state.upgradeResults[counter+27]+"", 'ether') } {process.env.REACT_APP_BLOCKCHAIN} 
            <br /> <br />
            Gold upgrades left:  <br />{this.state.upgradeResults[counter+36]-this.state.upgradeResults[counter+45]} / {this.state.upgradeResults[counter+36]} 
            <br />
            <br />
            </Card>
          </Grid>
        )
      }

      return components
    }


    render() {

      return (
        <div >
        <Header />
        <Container  maxWidth="sm">
          <Grid container>

              <Grid item md={12} xs={12} >
               <h3>Meta Card upgrade information</h3>
              </Grid>
              <br/>
              <Grid container spacing={2}>
                {this.state.upgradeResults.length > 0?
                    this.getUpgradeInfoComponent()
                :null}
              </Grid>



              <Grid item md={12} xs={12} >
               <h3> My Meta Cards</h3>
              </Grid>
              <br/>
              <Grid container spacing={2}>
                {this.state.tokenIDs.length > 0?
                  this.getWalletItems()
                :null}
              </Grid>
            
          </Grid>
          <br />
          <br />
          <br />

        </Container>
        </div>
      );
    }
  }
export default WalletPage;