export const getWeb3 = () =>
    new Promise(async(resolve, reject) => {
    if (window.ethereum) {
        const web3 = new Web3(window.ethereum);
        try {
            await window.ethereum.enable();
        resolve(web3);
        } catch (error) {
        reject(error);
        }
    } else if (window.web3) {
        // load metamask provider
        const web3 = window.web3;
        console.log("Injected web3 detected.");
        resolve(web3);
    } else {
        // main net
        // const provider = new Web3.providers.HttpProvider("https://api.avax.network/ext/bc/C/rpc");

        // test net 
        // const provider = new Web3.providers.HttpProvider("https://api.avax-test.network/ext/bc/C/rpc");
        let provider 
        if(process.env.REACT_APP_BLOCKCHAIN == "AVAX"){
            // main net 
            provider = new Web3.providers.HttpProvider("https://api.avax.network/ext/bc/C/rpc");
          
            // test net 
            if(process.env.REACT_APP_ISTEST == "TRUE"){
                provider = new Web3.providers.HttpProvider("https://api.avax-test.network/ext/bc/C/rpc");
            }
          }else if(process.env.REACT_APP_BLOCKCHAIN == "BSC"){
            // main net 
            provider = new Web3.providers.HttpProvider("https://bsc-dataseed.binance.org");
          
            // test net 
            if(process.env.REACT_APP_ISTEST == "TRUE"){
                provider = new Web3.providers.HttpProvider("https://data-seed-prebsc-1-s1.binance.org:8545/");
            }
          }else if(process.env.REACT_APP_BLOCKCHAIN == "FTM"){
             // main net 
             provider = new Web3.providers.HttpProvider("https://rpc.ftm.tools/");
          
             // test net 
             if(process.env.REACT_APP_ISTEST == "TRUE"){
                 provider = new Web3.providers.HttpProvider("https://rpc.testnet.fantom.network/");
             }
          }else if(process.env.REACT_APP_BLOCKCHAIN == "ONE"){
             // main net 
             provider = new Web3.providers.HttpProvider("https://api.harmony.one");
          
             // test net 
             if(process.env.REACT_APP_ISTEST == "TRUE"){
                 provider = new Web3.providers.HttpProvider("https://api.s0.b.hmny.io");
             }
          }else if(process.env.REACT_APP_BLOCKCHAIN == "ETH"){
             // main net 
             provider = new Web3.providers.HttpProvider("https://main-light.eth.linkpool.io/");
          
             // test net 
             if(process.env.REACT_APP_ISTEST == "TRUE"){
                 provider = new Web3.providers.HttpProvider("https://rinkeby-light.eth.linkpool.io/");
             }
          }

        const web3 = new Web3(provider);
        console.log("No web3 instance injected, using Local web3.");
        resolve(web3);
    }
});
export const getInstanceContract = async (web3) => {
    window.user = (await web3.eth.getAccounts())[0];
    
    window.instanceContract= new web3.eth.Contract(
        contractABI,
        contractAddress,
        {
            from: window.user
        }
    );
    return window.instanceContract;
}

export const getInstanceToken = async (web3) => {
    window.user = (await web3.eth.getAccounts())[0];
    
    window.instanceToken= new web3.eth.Contract(
        tokenABI,
        tokenAddress,
        {
            from: window.user
        }
    );
    return window.instanceToken;
}



