import React from "react";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { Accordion, AccordionSummary, AccordionDetails } from '@material-ui/core';
import { ExpandMore } from '@material-ui/icons';

import {Link} from 'react-router-dom'

import Header from "../components/Header"

import TrippyElement1 from "../components/three/TrippyElement1/TrippyElement1"
import TrippyElement2 from "../components/three/TrippyElement2/TrippyElement2"
import TrippyElement3 from "../components/three/TrippyElement3/TrippyElement3"

import WaterElement from "../components/three/WaterElement/WaterElement"
import SkullElement from "../components/three/SkullElement/SkullElement"
import TrippyElement4 from "../components/three/TrippyElement4/TrippyElement4"

import TrippyElement5 from "../components/three/TrippyElement5/TrippyElement5"
import TrippyElement6 from "../components/three/TrippyElement6/TrippyElement6"
import TrippyElement7 from "../components/three/TrippyElement7/TrippyElement7"
import {getInstanceContract,getWeb3}  from "../Web3Util";
import Web3 from "web3";

class HomePage extends React.Component {

    constructor(props){
      super(props)
      this.state={
        counter:Math.floor(Math.random() * 9),
        isTransitioningCard:false,
        dataResult:[]
      }
      this.setChoice = this.setChoice.bind(this)
      this.loadData = this.loadData.bind(this)
      this.mint = this.mint.bind(this)
    }
    componentDidMount = async () => {
      try{
        const web3 = await getWeb3();
        window.web3 = web3;
        const contractInstance = await getInstanceContract(window.web3);
  
        this.setState({ user: window.user });
        this.setState({ contractInstance});
        await this.loadData();
      } catch (e) {
        console.log('Error', e)
      }finally{
        this.setState({
          isLoadingHomepageData:false
        })
      }
  
    }


    mint = async () => {
      try{
        await this.state.contractInstance.methods.mint(1,this.state.counter).send({
          value:this.state.dataResult[this.state.counter+18]
        });
       
      } catch (e) {
        console.log('Error', e)
      }finally{
        await this.loadData();
        this.setState({
          isLoadingHomepageData:false
        })
      }
  
    }

    async loadData() {
      try {

        // const [minted0,minted1,minted2,minted3,minted4,minted5,minted6,minted7,minted8,
        //   supply0,supply1,supply2,supply3,supply4,supply5,supply6,supply7,supply8,
        //   price0,price1,price2,price3,price4,price5,price6,price7,price8] = await Promise.all([
          const result = await Promise.all([
          this.state.contractInstance.methods.currentCounter(0).call(), 
          this.state.contractInstance.methods.currentCounter(1).call(), 
          this.state.contractInstance.methods.currentCounter(2).call(), 
          this.state.contractInstance.methods.currentCounter(3).call(), 
          this.state.contractInstance.methods.currentCounter(4).call(), 
          this.state.contractInstance.methods.currentCounter(5).call(), 
          this.state.contractInstance.methods.currentCounter(6).call(), 
          this.state.contractInstance.methods.currentCounter(7).call(), 
          this.state.contractInstance.methods.currentCounter(8).call(), 

          this.state.contractInstance.methods.max(0).call(), 
          this.state.contractInstance.methods.max(1).call(), 
          this.state.contractInstance.methods.max(2).call(), 
          this.state.contractInstance.methods.max(3).call(), 
          this.state.contractInstance.methods.max(4).call(), 
          this.state.contractInstance.methods.max(5).call(), 
          this.state.contractInstance.methods.max(6).call(), 
          this.state.contractInstance.methods.max(7).call(), 
          this.state.contractInstance.methods.max(8).call(), 

          this.state.contractInstance.methods.prices(0).call(), 
          this.state.contractInstance.methods.prices(1).call(), 
          this.state.contractInstance.methods.prices(2).call(), 
          this.state.contractInstance.methods.prices(3).call(), 
          this.state.contractInstance.methods.prices(4).call(), 
          this.state.contractInstance.methods.prices(5).call(), 
          this.state.contractInstance.methods.prices(6).call(), 
          this.state.contractInstance.methods.prices(7).call(), 
          this.state.contractInstance.methods.prices(8).call(), 
        
        ])


        // console.log("@@@ result",{result})
        
        this.setState({hasHomepageData:true,dataResult:result})
      } catch (e) {
        console.log('Error', e)
      }
    }

    setChoice(counter){
      this.setState({
        isTransitioningCard: true,
      }, () => setTimeout(() => {
        this.setState({
          isTransitioningCard: false,
        })
        this.setState({
          counter
        })
      }, 800))
    }

    render() {

      let shownObject

      if(this.state.counter == 0){
        shownObject = <TrippyElement1 key={this.state.counter} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isGold={false} isBaby={false}/>
      } else if(this.state.counter == 1){
        shownObject = <TrippyElement2 key={this.state.counter} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isGold={false} isBaby={false}/>
      }else if(this.state.counter == 2){
        shownObject = <WaterElement key={this.state.counter} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isGold={false} isBaby={false}/>
      }else if(this.state.counter == 3){
        shownObject = <TrippyElement3 key={this.state.counter} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isGold={false} isBaby={false}/>
      }else if(this.state.counter == 4){
        shownObject = <SkullElement key={this.state.counter}  shouldRotateAutomatically={true} hasRandomInitialPosition={true} isGold={false} isBaby={false}/>
      }else if(this.state.counter == 5){
        shownObject = <TrippyElement4 key={this.state.counter} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isGold={false} isBaby={false}/>
      }else if(this.state.counter == 6){
        shownObject = <TrippyElement5 key={this.state.counter} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isGold={false} isBaby={false}/>
      }else if(this.state.counter == 7){
        shownObject = <TrippyElement6 key={this.state.counter} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isGold={false} isBaby={false}/>
      }else if(this.state.counter == 8){
        shownObject = <TrippyElement7 key={this.state.counter} shouldRotateAutomatically={true} hasRandomInitialPosition={true} isGold={false} isBaby={false}/>
      }
 
      let animationClass = "card-nft-enter card-nft"
      let bgClass = "max-height experience-"+this.state.counter

      if(!this.state.isTransitioningCard){
        animationClass += " card-nft-enter-active"
      }else{
        bgClass += " bg-transition"
      }

      return (
        <div className={bgClass}>
        <Header />
        <Container  maxWidth="sm">
        {process.env.REACT_APP_BLOCKCHAIN == "AVAX" && process.env.REACT_APP_SHOWTOKENPAGE== "TRUE" ?
            <Grid container>
              <Grid item md={12} xs={12} style={{textAlign:"center"}}>
                <br />
                <br />
                <span>New:</span> see our ERC20 <Link style={{fontWeight:"bold",textDecoration:"none",color:"black"}}  to="/erc20">Token page</Link>!
              </Grid>
            </Grid>
          : null          
          }
          <br/>


          <Grid container>
              <Grid item md={4} xs={12} >
                <div style={{textAlign:"center",display: "table-cell",verticalAlign: "middle",height:"360px"}} className="card-choices">
                <ul>
                <li><span onClick={()=>{this.setChoice(0)}} className={this.state.counter==0 && !this.state.isTransitioningCard?"item-selected":""}>Shifting Gradient</span></li>
                <li><span onClick={()=>{this.setChoice(1)}} className={this.state.counter==1 && !this.state.isTransitioningCard?"item-selected":""}>Bending Stripes</span></li>
                <li><span onClick={()=>{this.setChoice(2)}} className={this.state.counter==2 && !this.state.isTransitioningCard?"item-selected":""}>Cloudy Day</span></li>
                <li><span onClick={()=>{this.setChoice(3)}} className={this.state.counter==3 && !this.state.isTransitioningCard?"item-selected":""}>Melting Colors</span></li>
                <li><span onClick={()=>{this.setChoice(4)}} className={this.state.counter==4 && !this.state.isTransitioningCard?"item-selected":""}>Fire Soul</span></li>
                <li><span onClick={()=>{this.setChoice(5)}} className={this.state.counter==5 && !this.state.isTransitioningCard?"item-selected":""}>Color Dots</span></li>
                <li><span onClick={()=>{this.setChoice(6)}} className={this.state.counter==6 && !this.state.isTransitioningCard?"item-selected":""}>Dancing Lines</span></li>
                <li><span onClick={()=>{this.setChoice(7)}} className={this.state.counter==7 && !this.state.isTransitioningCard?"item-selected":""}>Stormy Night </span></li>
                <li><span onClick={()=>{this.setChoice(8)}} className={this.state.counter==8 && !this.state.isTransitioningCard?"item-selected":""}>Liquid Sphere</span></li>
                </ul>
                </div>
              </Grid>
              <Grid item md={4} xs={12} style={{margin:"auto",overflow:"hidden"}}>
                <div style={{height:"360px",width:"100%"}} className={animationClass+" card"}>
                  {shownObject}
                </div>

                <div style={{textAlign:"center"}}>
                {/* {process.env.REACT_APP_BLOCKCHAIN}
               {process.env.REACT_APP_ISTEST == "TRUE" ?" true":"false"} */}
                <Button onClick={()=>this.mint()}>Mint</Button>
                
                </div>
                
              </Grid>
          <br/>
          <br/>
          <br/>

              <Grid item md={4} xs={12}style={{textAlign:"center", display: "flex",justifyContent: "center",alignItems: "center"}}>
                <div style={{textAlign:"center",width:"100%"}} className={animationClass}>
                 Type #{this.state.counter+1}/9
                 <br />
                 <br />
                  {this.state.dataResult[this.state.counter+18] != undefined?
                
                  <>
                   Minted: {this.state.dataResult[this.state.counter]} / {this.state.dataResult[this.state.counter+9]}
                    <br />
                    <br />

                    Price: {Web3.utils.fromWei(this.state.dataResult[this.state.counter+18]+"", 'ether')} {process.env.REACT_APP_BLOCKCHAIN}
                  </>
                  
                  :null}
                 


                </div>
               
              </Grid>
          </Grid>
          <br/>
         

          <Grid container>
              <Grid item md={12} xs={12} >
                  
                   
                  <Accordion>
                    <AccordionSummary expandIcon={<ExpandMore />}>
                    What are Meta Cards?
                    </AccordionSummary>

                    <AccordionDetails>
                    Meta Cards are a world class NFT series. You can take a look at the different types of NFTs above. Feel free to move them around. <br/><br/>
                    Meta Cards had a fair, stealth launch with 9 unique types of NFTs available for minting from day one.<br/><br/>
                    Different to most NFTs in the market, Meta Cards are entirely 3D - in other words, it is impossible to simply "right click and save them".


                    </AccordionDetails>
                  </Accordion>
              </Grid>
          </Grid>
          <br/>

          <Grid container>
              <Grid item md={12} xs={12} >
                  
                  <Accordion>
                    <AccordionSummary expandIcon={<ExpandMore />}>
                    What can I do with my Meta Cards?
                    </AccordionSummary>

                    <AccordionDetails>
                    Meta Cards follow the ERC721 standard for NFTs - so you can trade them normally on your favorite NFT marketplaces.  <br/><br/>
                    Besides collecting and trading different cards, Meta Cards have a special feature: upgrades.<br/><br/>
                    There are two different types of upgrades available to increase your cards rarity: you can transform your card shape entirely (baby upgrade) or even change the logo color (gold upgrade) - permanently. You can even have both upgrades at once on the same NFT!<br/><br/>
                    Only a small portion of the NFTs can be upgraded (you can find more details in the wallet page). If you want an upgrade, make sure to upgrade your NFTs before there are no more upgrades left!<br/><br/>
                    Meta Cards can be upgraded at the wallet page.
                    </AccordionDetails>
                </Accordion>
              </Grid>
          </Grid>
          <br/>
          <Grid container>
              <Grid item md={12} xs={12} style={{textAlign:"right"}}>
                <a style={{fontWeight:"bold",textDecoration:"none",color:"black"}} href="https://twitter.com/meta_cards" target="_blank">Follow Meta Cards on Twitter</a>
              </Grid>
          </Grid>
       
          <br/>
          <Grid container>
              <Grid item md={12} xs={12} style={{textAlign:"right"}} >
                <a style={{fontWeight:"bold",textDecoration:"none",color:"black"}} href="https://metamask.io/download" target="_blank">Install Metamask</a>
              </Grid>
          </Grid>
          <br/>
          <br/>

          {/* <Grid container >
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                    <TrippyElement1  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement2  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <WaterElement  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement3  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <SkullElement  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement4  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement5  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement6  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement7  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
           </Grid> */}
        </Container>
        </div>
      );
    }
  }
export default HomePage;