import React from "react";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';

import { Accordion, AccordionSummary, AccordionDetails } from '@material-ui/core';
import { ExpandMore } from '@material-ui/icons';

import Header from "../components/Header"

import {getInstanceToken,getWeb3,tokenAddress}  from "../Web3Util";
import Web3 from "web3";

class HomePage extends React.Component {

    constructor(props){
      super(props)
      this.state={
        counter:Math.floor(Math.random() * 9),
        isTransitioningCard:false,
        dataResult:[],
        sacrificeAmount:0,
        liquidityBalance:0,
        userBalance:0,
        timeLeft:""
      }
      this.loadData = this.loadData.bind(this)
      this.sacrifice = this.sacrifice.bind(this)
      this.tick = this.tick.bind(this);
      this.updateSacrificeAmount = this.updateSacrificeAmount.bind(this)
      this.claim = this.claim.bind(this)

    }
    componentDidMount = async () => {
      try{
        const web3 = await getWeb3();
        window.web3 = web3;
        const contractInstance = await getInstanceToken(window.web3);
  
        this.setState({ user: window.user });
        this.setState({ contractInstance});
        await this.loadData();
      } catch (e) {
        console.log('Error', e)
      }finally{
        this.setState({
          isLoadingHomepageData:false
        })
      }
      this.interval = setInterval(() => this.tick(), 250);

    }

    async tick() {
   

      if(window.user != undefined){
        const currentRate = await  this.state.contractInstance.methods.getCurrentRate().call()
        this.setState({currentRate})
      }
      // Get today's date and time
      var now = new Date().getTime();
        
      // Find the distance between now and the count down date
      var distance = this.state.endSacrifice*1000 - now;
        
      // Time calculations for days, hours, minutes and seconds
      var days = Math.floor(distance / (1000 * 60 * 60 * 24));
      var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      var seconds = Math.floor((distance % (1000 * 60)) / 1000);
        
      // Output the result in an element with id="demo"
     
      if (distance < 0) {
        this.setState({
          timeLeft:""
        })
      }else{
        this.setState({
          timeLeft:days + "D:" +hours + "H:" + minutes + "M:" +seconds+"S"
        })
      }
    
    }
   

    async loadData() {
      try {

       
          const [currentRate,
            metaCardsBalance,
            canClaim,
            canSacrifice,
            startSacrifice,
            endSacrifice,
            liquidityBalance] = await Promise.all([
          this.state.contractInstance.methods.getCurrentRate().call(), 
          this.state.contractInstance.methods.getMetaCardsBalance().call(), 
          this.state.contractInstance.methods.canClaim().call(), 
          this.state.contractInstance.methods.canSacrifice().call(), 
          this.state.contractInstance.methods.startSacrifice().call(), 
          this.state.contractInstance.methods.endSacrifice().call(), 
          this.state.contractInstance.methods.liquidityBalance().call(), 
        ])


        if(window.user != undefined){
          let userBalance = await  this.state.contractInstance.methods.userBalances(window.user).call() 
          this.setState({userBalance})
        }


        this.setState({hasHomepageData:true,
            currentRate,
            metaCardsBalance,
            canClaim,
            canSacrifice,
            startSacrifice,
            endSacrifice,
            liquidityBalance})
      } catch (e) {
        console.log('Error', e)
      }
    }

    async sacrifice(){
      try{
        await this.state.contractInstance.methods.sacrifice().send({
          value:Web3.utils.toWei(this.state.sacrificeAmount+"", 'ether')
        })
      }catch(e){

      }finally{
        this.loadData()
      }

    }

    async claim(){
      try{
        await this.state.contractInstance.methods.claimTokens().send()
      }catch(e){

      }finally{
        this.loadData()
      }

    }

    updateSacrificeAmount(event){
      this.setState({
        sacrificeAmount:event.target.value
      })
    }


    render() {

 
      let animationClass = "card-nft-enter card-nft"
      let bgClass = "max-height experience-"+this.state.counter

      if(!this.state.isTransitioningCard){
        animationClass += " card-nft-enter-active"
      }else{
        bgClass += " bg-transition"
      }

   
      return (
        <div className={bgClass}>
        <Header />
        <Container  maxWidth="sm">
        <br/>
          <br/>
          <br/>
          {window.user != undefined? 
          <Grid container>
            
              <Grid item md={12} xs={12} style={{margin:"auto",overflow:"hidden",textAlign:"center"}}>
              <h3> My $MTC balance to be claimed: { Web3.utils.fromWei(this.state.userBalance+"", 'ether')} </h3> 
              <h4>Total {process.env.REACT_APP_BLOCKCHAIN} sacrificed: { Web3.utils.fromWei(this.state.liquidityBalance+"", 'ether')} </h4> <br/>
               {this.state.timeLeft != ""?<>Time until sacrifice phase is over: {this.state.timeLeft}</>:null}

               <br />
              <br />
              <br />
              </Grid>
             
              <Grid item md={12} xs={12} style={{margin:"auto",overflow:"hidden",textAlign:"center"}}>
               {this.state.canSacrifice && this.state.timeLeft != ""?
                <>
                  My rate: {this.state.currentRate} $MCT for 1 {process.env.REACT_APP_BLOCKCHAIN} <br/> {this.state.metaCardsBalance>0?<>(best rate)</>:<>(diminishing rate)</>} <br/> <br/>

                   <br />
                  <Input style={{width:"100%"}} type="number" onBlur={(event)=>this.updateSacrificeAmount(event)}placeholder={"Enter a sacrifice amount (in "+ process.env.REACT_APP_BLOCKCHAIN+") greater than 0"}/> <br/><br/>
                  <Button onClick={()=>this.sacrifice()}>Confirm sacrifice</Button>
                  <br/>
                </>
                :null}

                {this.state.canClaim && this.state.userBalance>0?
                <>
                  <Button onClick={()=>this.claim()}>Claim my tokens</Button>
                </>
                :null}
                
              </Grid>
             
          </Grid>
            : 
          <Grid container>
                
                <Grid item md={12} xs={12} style={{margin:"auto",overflow:"hidden"}}>
               You need to connect to learn the details.
                </Grid>
                
            </Grid>

          }
          <br/>
          <br/>

          <Grid container>
              <Grid item md={12} xs={12} >
                  
                   
                  <Accordion>
                    <AccordionSummary expandIcon={<ExpandMore />}>
                    The $MCT token
                    </AccordionSummary>

                    <AccordionDetails>
                      <div>
                      Meta Cards offers everyone a chance to get $MCT by sacrifing {process.env.REACT_APP_BLOCKCHAIN} during the sacrifice phase. <br /><br />
                      The rate of tokens you may get depends on two things: <br /><br />
                      - If you hold at least one Meta Card NFT, you'll get the best possible rate for the $MCT tokens.<br /><br />
                      - Or if you don't hold a Meta Card NFT, the rate of $MCT tokens you get per {process.env.REACT_APP_BLOCKCHAIN} sacrificed diminishes with time.<br /><br />

                      Details about the launch will be announced on Twitter.<br /><br />

                      The $MCT address : {tokenAddress}
                      </div>
                    </AccordionDetails>
                  </Accordion>
              </Grid>
          </Grid>
      
          <br/>
          <Grid container>
              <Grid item md={12} xs={12} style={{textAlign:"right"}}>
                <a style={{fontWeight:"bold",textDecoration:"none",color:"black"}} href="https://twitter.com/meta_cards" target="_blank">Follow Meta Cards on Twitter</a>
              </Grid>
          </Grid>
       
          <br/>
          <Grid container>
              <Grid item md={12} xs={12} style={{textAlign:"right"}} >
                <a style={{fontWeight:"bold",textDecoration:"none",color:"black"}} href="https://metamask.io/download" target="_blank">Install Metamask</a>
              </Grid>
          </Grid>
          <br/>
          <br/>

          {/* <Grid container >
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                    <TrippyElement1  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement2  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <WaterElement  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement3  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <SkullElement  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement4  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement5  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement6  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
              <Grid item md={4} xs={10}>
                <div style={{height:"360px",width:"100%"}}>
                  <TrippyElement7  shouldRotateAutomatically={true} hasRandomInitialPosition={true}/>
                </div>
              </Grid>
           </Grid> */}
        </Container>
        </div>
      );
    }
  }
export default HomePage;