import React from "react";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as THREE from 'three'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js'

// import { GlitchPass } from 'three/examples/jsm/postprocessing/GlitchPass.js'
// import { DotScreenPass } from 'three/examples/jsm/postprocessing/DotScreenPass.js'
// import { GlitchPass } from 'three/examples/jsm/postprocessing/GlitchPass.js'
// import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js'
// import { RGBShiftShader } from 'three/examples/jsm/shaders/RGBShiftShader.js'
// import { GammaCorrectionShader } from 'three/examples/jsm/shaders/GammaCorrectionShader.js'
// import { SMAAPass } from 'three/examples/jsm/postprocessing/SMAAPass.js'
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js'

import fragskull from './fragskull.glsl'
import vertskull from './vertskull.glsl'
import { OBJLoader } from "three/examples/jsm/loaders/OBJLoader";
  import skullmodel from './skull5.obj'


import {getRenderer,getScene1,getLight1,getCamera1,getCamera2,getControls1,getControls2,getEffectComposer,
  getPlaneRoundedCorners, getPlaneRoundedCornersTransparent,getAVAXLogo,getTrippyMesh, getAVAXLogoWhite} from "../functions"
  var options = {
    exposure: 2.8,
    bloomStrength: 0.8,
    bloomThreshold: 0,
    bloomRadius: 1.29,
    // color0: [197, 81, 245],
    // color1: [65, 0, 170],
    // color2: [0, 150, 255],
    color0: [255, 0, 0],
    color1: [244, 67, 54],
    color2: [255, 251, 0],
    isanimate: false,
  };
  if(process.env.REACT_APP_BLOCKCHAIN == "ONE"){
    options.color0 = [4, 180, 225]
    options.color1 = [94, 226, 171]
    options.color2 = [244, 3,3]
  }else if(process.env.REACT_APP_BLOCKCHAIN == "FTM"){
    options.color0 = [7, 39, 239]
    options.color1 = [25, 101, 242]
    options.color2 = [244, 3,3]
  }
 
  let skullmaterial = new THREE.ShaderMaterial({
    uniforms: {
      time: {
        type: "f",
        value: 0.0,
      },
      color1: {
        value: new THREE.Vector3(...options.color1),
      },
      color0: {
        value: new THREE.Vector3(...options.color0),
      },
      resolution: {
        value: new THREE.Vector2(512, 512),
      },
    },
    fragmentShader: fragskull,
    vertexShader: vertskull,
    // depthWrite: false,
  });
  var skull 
  var objloader = new OBJLoader();
  objloader.load(skullmodel, function (object) {
    // var mesh2 = object.clone();
    // mesh2.position.set(0, 0, -10);
    object.traverse(function(child) {
      if (child instanceof THREE.Mesh) {
        child.material = skullmaterial;
      }
    });
    
    object.position.set(0, 0, -10);
    // mesh2.scale.set(8, 8, 8);
    object.scale.set(8, 8, 8);
      // modelgroup.add(object);
      skull = object
      // sceneRTT.add(modelgroup);
    // mesh2.rotation.set(Math.PI, 0, Math.PI);
    object.rotation.set(Math.PI, 0, Math.PI);
   
  });


const clock = new THREE.Clock()

class SkullElement extends React.Component {

  constructor(props) {
    super(props)
    this.state={
      shouldStopAutoRotate:false
    }

    this.start = this.start.bind(this)
    this.stop = this.stop.bind(this)
    this.animate = this.animate.bind(this)
    this.onWindowResize = this.onWindowResize.bind(this)
    this.getSizes = this.getSizes.bind(this)
  }

  onWindowResize() {
    if(this.mount != null){
      this.camera.aspect =this.mount.clientWidth / this.mount.clientHeight
      this.camera.updateProjectionMatrix()
      this.camera2.aspect =this.mount.clientWidth / this.mount.clientHeight
      this.camera2.updateProjectionMatrix()
      this.renderer.setSize(this.mount.clientWidth, this.mount.clientHeight)
    }
  }

  getSizes(){
    const sizes = {
      width: this.mount.clientWidth,
      height: this.mount.clientHeight
    }
    window.addEventListener('resize', this.onWindowResize, false);
    return sizes
  }

  componentDidMount() {
    const sizes = this.getSizes()
    // Canvas
    // Scene 1
    const renderer = getRenderer(sizes)
    const canvas = renderer.domElement
    
    const scene = getScene1()
    const light = getLight1()
    
    scene.background = new THREE.Color("#000000")
   
    const camera = getCamera1(sizes)
    
    const controls = new OrbitControls(camera, canvas)
    controls.enableDamping = true
    controls.minDistance = 3;
    controls.maxDistance = 4;

    //////////////////////////// 
   
    var eye,
    eye2,
    basicmat,
    modelgroup = new THREE.Group();
    
  
    var spheregeo = new THREE.SphereGeometry(1.5, 32, 32);
    basicmat = new THREE.MeshBasicMaterial();
    basicmat.color.setRGB(...options.color2);
    eye = new THREE.Mesh(spheregeo, basicmat);
    eye2 = new THREE.Mesh(spheregeo, basicmat);
    eye.position.set(-2.2, -2.2, -6.9);
    eye2.position.set(2.2, -2.2, -6.9);
    modelgroup = new THREE.Object3D();
   
    const avaxLogo = getAVAXLogo(this.props.isGold)

    if(skull == undefined){
      objloader.load(skullmodel, function (object) {
        // var mesh2 = object.clone();
        // mesh2.position.set(0, 0, -10);
        object.traverse(function(child) {
          if (child instanceof THREE.Mesh) {
            child.material = skullmaterial;
          }
        });
        
        object.position.set(0, 0, -10);
        // mesh2.scale.set(8, 8, 8);
        object.scale.set(8, 8, 8);
          // modelgroup.add(object);
          skull = object
          // sceneRTT.add(modelgroup);
        // mesh2.rotation.set(Math.PI, 0, Math.PI);
        object.rotation.set(Math.PI, 0, Math.PI);
        modelgroup.add(object)
        modelgroup.add(eye);
        modelgroup.add(eye2);
        modelgroup.add(avaxLogo)

      });
    }else{
      modelgroup.add(skull)
      modelgroup.add(eye);
      modelgroup.add(eye2);
      modelgroup.add(avaxLogo)

    }
 
    // modelgroup.rotation.x = - Math.PI * 1.5
    // mesh.scale.x = 8
    // mesh.scale.y = 8
    modelgroup.scale.x = 0.23
    modelgroup.scale.y = 0.16
    modelgroup.scale.z = 0.16
    modelgroup.rotation.y = 0.8
    modelgroup.position.y = 0.55
    modelgroup.position.z = 0.45
    modelgroup.position.x = 0.45
    if(this.props.isBaby){
      modelgroup.scale.x = 0.13
    }

    scene.add(modelgroup)

    /////////////////////////

    scene.add(light)
    scene.add(camera)

    // Scene 2 (main scene)
    const scene2 = new THREE.Scene()

    const camera2 = getCamera2(sizes)
    const effectComposer =  getEffectComposer(renderer,sizes,scene2,camera2)
    const controls2 = getControls2(camera2,canvas)
    const renderTarget = new THREE.WebGLRenderTarget(512, sizes.height);

    const plane = getPlaneRoundedCorners(renderTarget,this.props.isBaby)
    const planeTransparent = getPlaneRoundedCornersTransparent(this.props.isBaby)
    scene2.add(light)
    scene2.add(planeTransparent);

    const avaxLogow = getAVAXLogoWhite(this.props.isGold)
    avaxLogow.position.z = -1.1
    avaxLogow.rotation.y = - Math.PI

    scene2.add(avaxLogow);

    scene2.add(plane);


    
  
    avaxLogo.position.y = 0.5
    avaxLogo.position.x = 0
    avaxLogo.position.z = -4.5

    avaxLogo.scale.y = 0.3  
    avaxLogo.scale.x = 0.3 


    if(this.props.hasRandomInitialPosition){
      var randomX = Math.ceil(Math.random() * 5) * (Math.round(Math.random()) ? 1 : -1)
      var randomY = Math.ceil(Math.random() * 3) * (Math.round(Math.random()) ? 1 : -1)
      camera2.position.set(randomX, randomY, 17)
    }

    // post processing
    // const glitchPass = new GlitchPass()

    controls.rotateSpeed =controls2.rotateSpeed
    // controls.rotateSpeed =0
    
    this.scene = scene

    this.camera = camera
    this.camera2 = camera2

    this.renderer = renderer
    this.skullmaterial = skullmaterial 

    this.controls = controls
    this.controls2 = controls2

    this.renderTarget = renderTarget

    this.effectComposer = effectComposer

    // mandatory stuff
    this.mount.appendChild(this.renderer.domElement)
    this.start()
  }

  componentWillUnmount() {
    this.stop()
    this.mount.removeChild(this.renderer.domElement)
  }

  start() {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate)
    }
  }

  stop() {
    cancelAnimationFrame(this.frameId)
  }

  animate() {
    const elapsedTime = clock.getElapsedTime()
    this.skullmaterial.uniforms.time.value = elapsedTime/3;


    this.controls.update()
    this.controls2.update()
    
    if(this.props.shouldRotateAutomatically && !this.state.shouldStopAutoRotate){
      this.camera2.position.x = 3 * Math.sin(elapsedTime*1.35 )
    }

    this.renderScene()
    this.frameId = window.requestAnimationFrame(this.animate)
  }

  renderScene() {
    this.renderer.setRenderTarget( this.renderTarget );
    this.renderer.render(this.scene, this.camera, this.renderTarget);
    this.renderer.setRenderTarget( null );

    this.renderer.setClearColor( 0x000000, 0 );

    // Render
    this.effectComposer.render()
  }

  render() {
    return (
      <div
        onTouchStart={()=>this.setState({shouldStopAutoRotate:true})}
        onMouseDown={()=>this.setState({shouldStopAutoRotate:true})}
        style={{ width: '100%', height: '100%' }}
        ref={(mount) => { this.mount = mount }}
      />
    )
  }
}

export default SkullElement;