import React from "react";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import { Link} from 'react-router-dom'

class Header extends React.Component {

    render() {
        return (
            <Container className="meta-header"  >
            <Grid container >
                <Grid item md={4} xs={6}>
                    <div>
                        <h1 >
                        <Link className="meta-link-header" to="/" >
                        Meta Cards
                        </Link>
                        </h1>
                    </div>
                </Grid>
                <Grid item md={4} xs={false}>
                   
                </Grid>
                <Grid item md={4} xs={6}>
                    <div style={{textAlign:"right"}}>
                        <h1 >
                        {window.user == null ?

                            <Link to="/" onClick={()=>{if (window.ethereum!= undefined){window.ethereum.enable().then(()=>window.location.reload())}else{alert("You need Metamask to use this dapp.")} }}  >Connect</Link>
                            : <Link to="/wallet" className="meta-header-wallet" > Wallet <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M21 7.28V5c0-1.1-.9-2-2-2H5c-1.11 0-2 .9-2 2v14c0 1.1.89 2 2 2h14c1.1 0 2-.9 2-2v-2.28c.59-.35 1-.98 1-1.72V9c0-.74-.41-1.37-1-1.72zM20 9v6h-7V9h7zM5 19V5h14v2h-6c-1.1 0-2 .9-2 2v6c0 1.1.9 2 2 2h6v2H5z"/><circle cx="16" cy="12" r="1.5"/></svg></Link>

                            }
                        
                        </h1>
                    </div>
                </Grid>
            </Grid>
        </Container>
        );
    }
}

export default Header;